import React from "react"
import PropTypes from "prop-types"
import { Link, graphql } from "gatsby"

// Components
import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
import MailList from "../components/maillist"

const Tags = ({ pageContext, data }) => {
  const { tag } = pageContext
  const { edges, totalCount } = data.allMarkdownRemark
  const tagHeader = `
  يوجد عدد 
 ( ${totalCount} )
  مقال عن
:  
    ${tag}
   
    `
  return (
    <Layout>
      <div className="col-md-4" id="content-desktop">
        <Sidebar />
      </div>
      <div className="col-md-8">
        <hr />
        <div className="alert alert-warning" role="alert">
          <h3 className="my-1">
            {tagHeader}
            <i className="fa fa-tags fa-sm"></i>
          </h3>
        </div>
        <br />
        <ul dir="rtl">
          {edges.map(({ node }) => {
            const { slug } = node.fields
            const { title } = node.frontmatter
            return (
              <h6 key={slug}>
                <b>
                  <Link to={slug} className="stretched-link text-secondary">
                    <i className="fa fa-tags fa-sm"></i> {title}.
                    <br />
                  </Link>
                </b>
              </h6>
            )
          })}
        </ul>
        <br />
        <Link to="/tags" className="btn btn-warning btn-sm">
          <i className="far fa-arrow-alt-circle-left"></i> جميع الوسوم
        </Link>
        <br />
        <br />
        <div id="content-mobile">
          <div className="p-3 mb-2 bg-success text-white">القائمة البريدية</div>
          <MailList />
        </div>
        <hr />
      </div>
    </Layout>
  )
}

Tags.propTypes = {
  pageContext: PropTypes.shape({
    tag: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              title: PropTypes.string.isRequired,
            }),
            fields: PropTypes.shape({
              slug: PropTypes.string.isRequired,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
}

export default Tags

export const pageQuery = graphql`
  query($tag: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } } }
    ) {
      totalCount
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            title
          }
        }
      }
    }
  }
`
